class A11y {
  static get theme() {
    return A11y.get('theme');
  }

  static set theme(value) {
    const val = typeof value === 'string' ? value : '';
    document.body.setAttribute('data-theme', val);
    return A11y.set('theme', val);
  }

  static get prefix() {
    return '@a11y';
  }

  static get(key) {
    return localStorage.getItem(`${A11y.prefix}/${key}`);
  }

  static set(key, val) {
    return localStorage.setItem(`${A11y.prefix}/${key}`, val);
  }
}

A11y.theme = localStorage.getItem(`${A11y.prefix}/theme`);

window.A11y = A11y;

export default A11y;